.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  display: block;
  text-align: center;
  /* font-family: jameel-noori-nastaleq; */
  font-family: AA Sameer Asmaak;
}


.colom{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family:jameel-noori-nastaleq ;
  border: 2px solid rgb(207, 236, 99);
  background-color: #4b539a;
  box-shadow: 0px 0px 10px 5px rgb(92, 170, 113);
  margin-top: 30px;
  flex-basis: 31%;
}
.nm{
  margin-top: -60px;
  margin-left: -80px;
  color: rgb(216, 195, 187);
  font-size: 30px;
  font-family: AA Sameer Asmaak ;
  width: 50px;
}

.input{
  padding: 10px;
  text-align: center;
  margin-top: -10px;
  border-color: rgb(157, 219, 173);
  font-size: 20px;
  width: 190px;
  height: 10px;
  border-radius: 20px;

}

.button{
  border-radius: 10px ;
  padding: 10px;
  font-size: 15px;
  margin-top: 20px;
  width: 35%;
  background-color: #282c34;
  color: aliceblue;
  cursor: pointer;
}

.button1{
  border-radius: 10px;
  height: 70px;
  width: 45%;
  text-align: center;
  margin-top: 10px;
  font-size: 20px;
  background-color: yellow;
  cursor: pointer;
}

.answer{
  font-family:jameel-noori-nastaleq ;
  color: white;
}

.number{
  font-family:jameel-noori-nastaleq ;
  color: whitesmoke;
}
.adition{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;


}

.colom2{
